











import Vue from 'vue';
import NumberPad from '@/components/Money/NumberPad.vue';
import FormItem from '@/components/Money/FormItem.vue';
import Tags from '@/components/Money/Tags.vue';
import {Component} from 'vue-property-decorator';
import Button from '@/components/Button.vue';
import Tabs from '@/components/Tabs.vue';
import recordTypeList from '@/constants/recordTypeList';
import RecordDate from '@/components/RecordDate.vue';
@Component({
  components: {RecordDate, Tabs, Button, FormItem, Tags, Notes: FormItem, NumberPad},
})
export default class Money extends Vue {
  // eslint-disable-next-line no-undef
  record: RecordItem = {
    tags: [], notes: '', type: '-', amount: 0,createdAt:new Date().toISOString()
  };

  recordTypeList = recordTypeList;

  get recordList() {
    return this.$store.state.recordList;
  }

  created() {
    this.$store.commit('fetchRecords');
  }

  saveRecord() {
    if (this.record.tags.length === 0 || !this.record.tags) {
      return window.alert('请选择标签');
    }
    if (this.record.amount === 0 || !this.record.amount) {
      return window.alert('什么都没写呀，请输入金额后保存');
    }
    this.$store.commit('createRecord', this.record);
    if (this.$store.state.createRecordError === null) {
      window.alert('已保存');
      this.record.notes = '';
      this.record.amount = 0;
    }
  }


}
